h1,
h2,
h3 {
  font-family: "Noto Serif", serif;
  font-weight: 700;
}

th {
  font-size: small;
  color: gray;
}

ul {
  padding-inline-start: 20px;
}

@media screen and (max-width: 600px) {
  h1 {
    font-size: 1.7rem;
  }
  h2 {
    font-size: 1.2rem;
  }
}

.spark-spark,
.spark-text {
  vertical-align: middle;
  display: inline-block;
}
.mr-auto {
  margin: 0 auto;
  white-space: nowrap;
}

@media screen and (max-width: 1024px) {
  .mr-auto {
    flex-direction: row;
    overflow-x: scroll;
    padding: 0.3125rem 1rem;
  }
}
.mr-auto > * {
  margin-right: 20px;
  /* min-width: 50px; */
}

@media screen and (max-width: 600px) {
  .mr-auto > * {
    letter-spacing: -0.4px;
    margin-right: 10px;
  }
}

.navbar {
  background-color: #109618;
  padding: 0;
}

.navbar-for-brand {
  padding: 0 0 0.3125rem 0;
  border-bottom: 1px solid #ccc;
}
.navbar-brand {
  font-family: "Noto Serif", serif;
  font-weight: 700;
  margin: 0 auto;
  padding-bottom: 0;
}

.table th,
.table td {
  vertical-align: middle;
}

.stat-link {
  color: #0050a6;
  position: relative;
}
.active.stat-link:before {
  content: "\00BB\0020";
  position: absolute;
  left: -9px;
}

.btn:focus {
  outline: none;
  box-shadow: none;
}

.black,
.black:hover {
  color: #212529;
}

.btn.tight {
  padding: 0;
  text-align: left;
}
.btn.dark {
  color: #004896;
}
#nav-email-form {
  display: flex;
}
#nav-email-form > button {
  color: white;
  opacity: 0.8;
  position: absolute;
  right: 10px;
  bottom: 10px;
}

@media screen and (max-width: 1024px) {
  .form-inline {
    flex-flow: row;
    /* padding-bottom: 10px; */
  }
  .form-inline > button {
    margin-left: 10px;
  }
  #nav-email-form > button {
    bottom: 20px;
  }
}

.daddy {
  padding-bottom: 50px;
}

.table thead th {
  text-transform: uppercase;
}

#rankings th:last-child > svg {
  display: none;
}

.width-limited {
  max-width: 960px;
  margin: 0 auto;
}

.form-check-label-case-samples {
  margin-top: 2px;
}

.form-label-case-samples {
  top: -3px;
  position: relative;
}

.results-count-case-samples {
  text-align: right;
  position: relative;
  top: 26px;
  color: gray;
}
